<template>
  <el-form :model="ruleForm" :rules="rules" ref="ruleForm">
    <el-row :gutter="20">
      <el-col :span="24">
        <el-form-item prop="CategoryName">
          <el-input v-model="ruleForm.CategoryName" placeholder="カテゴリー名">
            <template slot="prepend">カテゴリー名</template>
          </el-input>
        </el-form-item>
      </el-col>
    </el-row>
    <el-row :gutter="20">
      <el-col :span="24">
        <!--        <el-form-item prop="Parent_id">-->
        <!--          <el-input v-model="ruleForm.Parent_id" placeholder="親カテゴリー" :disabled="ruleForm.Parent_flg">-->
        <!--            <template slot="prepend">親カテゴリー</template>-->
        <!--          </el-input>-->
        <!--        </el-form-item>-->
        <el-form-item prop="Parent_id">
          <el-autocomplete
            popper-class="my-autocomplete"
            v-model="ruleForm.Parent_id"
            :fetch-suggestions="querySearch"
            placeholder="親カテゴリー"
            style="width:100%"
            @select="handleSelect"
            :disabled="ruleForm.Parent_flg">
            <i
              class="el-icon-edit el-input__icon"
              slot="suffix"
              @click="handleIconClick">
            </i>
            <template slot-scope="{ item }">
              <div class="name">{{ item.value = item.CategoryName }}</div>
            </template>
            <template slot="prepend">親カテゴリー</template>
          </el-autocomplete>
        </el-form-item>
      </el-col>
    </el-row>
    <el-row :gutter="20">
      <el-col :span="24">
        <el-form-item prop="CategoryLevel" label="親FLG：">
          <el-switch v-model="ruleForm.Parent_flg" @change="isParent(ruleForm.Parent_flg)"></el-switch>
        </el-form-item>
      </el-col>
    </el-row>
    <el-row :gutter="20">
      <el-col :span="24">
        <el-form-item prop="Sorting" label="区分：">
          <el-radio-group v-model="ruleForm.Sorting">
            <el-radio label="商品"></el-radio>
            <el-radio label="サービス"></el-radio>
            <el-radio label="サポート"></el-radio>
          </el-radio-group>
        </el-form-item>
      </el-col>
    </el-row>
    <el-row type="flex" :gutter="20" justify="start">
      <el-col :span="24">
        <el-form-item>
          <el-button-group>
            <el-button type="warning" @click="cancelForm()">キャンセル</el-button>
            <el-button type="primary" icon="el-icon-finished" @click="submitForm('ruleForm')">登録</el-button>
          </el-button-group>
        </el-form-item>
      </el-col>
    </el-row>
  </el-form>
</template>

<script>
import {postCategoryData, getCategoryDataParent, getCategoryDataOne} from 'api/category'
import {Message} from "element-ui";

export default {
  name: "CategoryForm",
  props: {
    categoryuuid: {
      type: String, //指定传入的类型
      //type 也可以是一个自定义构造器函数，使用 instanceof 检测。
      default: '' //这样可以指定默认的值
    }
  },
  data() {
    return {
      ruleForm: {
        CategoryName: '',
        Parent_id: '',
        Parent_flg: false,
        Sorting: ''
      },
      rules: {
        CategoryName: [
          {required: true, message: 'カテゴリー名を入力してください', trigger: 'blur'}
        ],
        Sorting: [
          {required: true, message: '区分を決めてください', trigger: 'blur'}
        ]
      },
      Categories: []
    }
  },
  created() {
    if (this.categoryuuid) {
      this.getCategoryDataOne()
    }
    this.getCategoryDataParent()
  },
  methods: {
    postCategoryData() {
      let values = {}
      values['CategoryName'] = this.ruleForm.CategoryName
      values['Parent_id'] = this.ruleForm.Parent_id
      values['Parent_flg'] = this.ruleForm.Parent_flg
      values['Sorting'] = this.ruleForm.Sorting
      values['uuid'] = this.uuid
      console.log(values);
      postCategoryData(values).then(res => {
        console.log(res.data.message);
        if (res.data.code == 200) {
          this.$emit('submit')
          Message({
            showClose: true,
            message: res.data.message,
            type: 'success'
          });
        } else if (res.data.code == 201) {
          this.$emit('submit')
          Message({
            showClose: true,
            message: res.data.message,
            type: 'warning'
          });
        } else {
          this.$emit('cancel')
          this.$message({
            showClose: true,
            message: 'カテゴリー登録・更新失敗しました。',
            type: 'error'
          });
        }
      }).catch(err => {
        console.log(err);
      })
    },
    getCategoryDataOne() {
      let values = {}
      values['uuid'] = this.categoryuuid
      getCategoryDataOne(values).then(res => {
        this.ruleForm.CategoryName = res.data.data[0].CategoryName
        this.ruleForm.Parent_id = res.data.data[0].Parent_id
        this.ruleForm.Parent_flg = res.data.data[0].Parent_flg
        this.ruleForm.Sorting = res.data.data[0].Sorting
      }).catch(err => {
        console.log(err)
      })
    },
    getCategoryDataParent() {
      getCategoryDataParent().then(res => {
        this.Categories = res.data.data
      }).catch(err => {
        console.log(err)
      })
    },
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.postCategoryData()
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    isParent(Parent_flg) {
      if (Parent_flg) {
        this.ruleForm.Parent_id = ''
      }
    },
    querySearch(queryString, cb) {
      let Categories = this.Categories
      let results = queryString ? Categories.filter(this.createFilter(queryString)) : Categories
      // 调用 callback 返回建议列表的数据
      cb(results)
    },
    createFilter(queryString) {
      return (restaurant) => {
        return (restaurant.CategoryName.toLowerCase().indexOf(queryString.toLowerCase()) === 0)
      }
    },
    handleSelect(item) {
      // 把选择的顾客名对应的顾客CD一并发送给后端
      console.log(item)
      // this.ruleForm.CategoryName = item.CategoryName
      // this.ruleForm.Personnel = item.Personnel
    },
    handleIconClick(ev) {
      console.log(ev)
    },
    cancelForm() {
      this.$emit('cancel')
    }
  }
}
</script>

