import {request} from 'network/request'

export function postCategoryData(paramenter) {
  return request({
    url: '/PostCategoryData',
    method: 'post',
    data: paramenter
  })
}

export function getCategoryDataAll(paramenter) {
  return request({
    url: '/GetCategoryDataAll',
    method: 'get',
    data: paramenter
  })
}

export function getCategoryDataParent(paramenter) {
  return request({
    url: '/GetCategoryDataParent',
    method: 'get',
    data: paramenter
  })
}

export function getCategoryDataOne(paramenter) {
  return request({
    url: '/GetCategoryDataOne',
    method: 'post',
    data: paramenter
  })
}

export function searchCategoryData(parameter) {
  return request({
    url: '/SearchCategoryData',
    method: 'post',
    data: parameter
  })
}
