    <template>カテゴリー一覧画面です。</template>
    <template>この画面でカテゴリーの確認できますし、編集ボタンから編集画面に遷移もできます。</template>
    <el-divider></el-divider>
    <el-row :gutter="20">
      <el-col :span="24">
        <el-form :model="ruleForm" ref="ruleForm" class="demo-ruleForm">
          <el-row type="flex" :gutter="40" justify="left">
            <el-col :span="8">
              <el-form-item prop="CategoryName">
                <el-input v-model="ruleForm.CategoryName" placeholder="カテゴリー名">
                  <template slot="prepend">カテゴリー名</template>
                </el-input>
              </el-form-item>
            </el-col>
            <el-col :span="8">
              <el-form-item prop="Parent_id">
                <el-input v-model="ruleForm.Sorting" placeholder="区分名">
                  <template slot="prepend">区分名</template>
                </el-input>
              </el-form-item>
            </el-col>
<!--            <el-col :span="2">-->
<!--              <el-form-item>-->
<!--                <a-affix :offset-top="10">-->
<!--                  <el-button type="primary" @click="submitForm('ruleForm')" icon="el-icon-search" style="width: 90px">-->
<!--                    検索-->
<!--                  </el-button>-->
<!--                </a-affix>-->
<!--                <a-affix :offset-top="50">-->
<!--                  <el-button type="warning" @click="resetForm('ruleForm')" style="width: 90px">クリア</el-button>-->
<!--                </a-affix>-->
<!--                <a-affix :offset-top="90">-->
<!--                  <el-button type="success" @click="createBtn" icon="el-icon-circle-plus-outline" style="width: 90px">-->
<!--                    新規-->
<!--                  </el-button>-->
<!--                </a-affix>-->
<!--              </el-form-item>-->
<!--            </el-col>-->
          </el-row>
          <el-row type="flex" :gutter="20" justify="start">
            <el-col :span="11">
              <el-form-item>
                <el-button-group>
                  <el-button type="primary" @click="submitForm('ruleForm')" icon="el-icon-search">検索</el-button>
                  <el-button type="warning" @click="resetForm('ruleForm')">クリア</el-button>
                  <el-button type="success" @click="createBtn">新規</el-button>
