        </el-form>
      </el-col>
    </el-row>
    <el-row :gutter="20">
      <el-col :span="24">
        <el-divider></el-divider>
      </el-col>
    </el-row>
    <el-row :gutter="20">
      <el-col :span="24">
        <el-table
          :data="tableData.filter(data => !search || data.CategoryName.toLowerCase().includes(search.toLowerCase()))"
          v-loading="loading" style="width: 100%">
          <template slot="empty">カテゴリーがありません</template>
          <el-table-column label="カテゴリー" prop="CategoryName">
            <template slot-scope="scope">
              <a @click="handleEdit(scope.$index, scope.row)"
                 style="color:#ffd04b;cursor:pointer;font-weight: 500;">{{ scope.row.CategoryName }}</a>
            </template>
          </el-table-column>
          <el-table-column label="親カテゴリー" prop="Parent_id">
            <!--            <template slot-scope="scope">-->
            <!--              <a @click="handleEdit(scope.$index, scope.row)"-->
            <!--                 style="color:#ffd04b;cursor:pointer;font-weight: 500;">{{ scope.row.Parent_id }}</a>-->
            <!--            </template>-->
          </el-table-column>
          <el-table-column label="区分" prop="Sorting">
          </el-table-column>
          <el-table-column align="right">
            <!--eslint-disable-next-line-->
            <template slot="header" slot-scope="scope">
              <el-input v-model="search" size="mini" placeholder="カテゴリー名で絞ります"/>
            </template>
            <!--eslint-disable-next-line-->
            <template slot-scope="scope">
              <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)">削除
              </el-button>
            </template>
          </el-table-column>
        </el-table>
      </el-col>
    </el-row>
    <div>
      <el-dialog title="カテゴリー登録・更新" :visible.sync="dialogFormVisible" top="5%" v-if="dialogFormVisible"
                 destroy-on-close>
        <CategoryForm :categoryuuid="uuid" @cancel="closeDialog" @submit="reloadList"></CategoryForm>
      </el-dialog>
    </div>
  </div>
</template>

<script>
import PageHeader from 'components/common/menu/PageHeader'
import CategoryForm from "@/views/master/CategoryForm";
import {getCategoryDataAll, postCategoryData, searchCategoryData} from "api/category";
import {Message} from "element-ui";

export default {
  name: "CategoryList",
  components: {
    PageHeader,
    CategoryForm
  },
  data() {
    return {
      ruleForm: {
        CategoryName: '',
        Sorting: ''
      },
      uuid: '',
      dialogFormVisible: false,
      loading: false,
      tableData: [],
      search: ''
    };
  },
  created() {
    this.getCategoryDataAll()
  },
  methods: {
    getCategoryDataAll() {
      this.loading = true
      getCategoryDataAll().then(res => {
        this.tableData = res.data.data
        if (res.status == 200) {
          this.loading = false
        }
      }).catch(err => {
        console.log(err)
      })
    },
    searchCategoryData() {
      let values = {}
      values['CategoryName'] = this.ruleForm.CategoryName
      values['Sorting'] = this.ruleForm.Sorting
      searchCategoryData(values).then(res => {
        console.log(res);
        this.tableData = res.data.data
      }).catch(err => {
        console.log(err)
      })
    },
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.searchCategoryData()
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    handleEdit(index, row) {
      this.uuid = row.uuid
      this.dialogFormVisible = true
    },
    handleDelete(index, row) {
      this.loading = true
      let values = {}
      values['uuid'] = row.uuid
      values['CategoryName'] = row.CategoryName
      values['Is_Delete'] = 1
      console.log(index);
      console.log(row);
      postCategoryData(values).then(res => {
        console.log(res);
        if (res.data.code === '202') {
          this.getCategoryDataAll()
          Message({
            showClose: true,
            message: res.data.message,
            type: 'error'
          });
        }
      }).catch(err => {
        console.log(err)
      })
    },
    closeDialog() {
      this.dialogFormVisible = false
    },
    reloadList() {
      this.dialogFormVisible = false
      this.getCategoryDataAll()
    },
    createBtn() {
      this.uuid = ''
      this.dialogFormVisible = true
    },
  }
}
</script>

